body {
  font-family: Arial, Helvetica, sans-serif;
  position: relative;
  height: 100vh;
  padding: 0;
  margin: 0;
}

.row {
  clear: both; 
  margin: 0 auto;
  text-align: center;
  min-width: 1585px;
}

ul {
  margin: 0;
  padding: 20px;
}

li {
  list-style: none;
}

.rowName{
  display: inline;
}

.row ul {
  margin: 0 10px;
  padding: 0;
  display: inline;
}

.zoneSelector {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  background: #f7f7f7;
  z-index: 1;
}

.row li, .zoneSelector li {
  padding: 5px;
  margin: 0 1px 10px 0;
  position: relative;
  background: #d0d0ce;
  font-size: .7em;
  color:#000;
  width: 12px;
  height: 12px;
  text-align: center;
  cursor: pointer;
  user-select: none;
  border-radius: 100%;
  display: inline-block;

}

.zoneSelector li {
  font-size: 1em;
  border-radius: 3px;
  padding: 10px;
  width: 15px;
  height: 15px;
  margin: 0 0 0 10px;
  float: left;
}

.zoneSelector .zoneOne {
  background: #d0d0ce;
  color:#000;
}

.zoneSelector .zoneTwo {
  background: #0084d4;
  color: #fff;
}

.zoneSelector .zoneThree {
  background: #ef426f;
  color: #fff;
}

.zoneSelector .zoneFour {
  background: #ffdd46;
  color: #fff;
}

.zoneSelector .zoneFive {
  background: #26d07c;
  color: #fff;
}

.zoneSelector .zoneSix {
  background: #000;
  color: #fff;
}

.D span:nth-child(1) {
  padding-left: 219px;
}

.J span:nth-child(1) {
  padding-left: 258px;
}

.C .n38, .I .n43 {
  margin-right: 75px;
}

.X .n28, .X .n29, .X .n42, .X .n43, .Y .n9, .Y .n10, .Y .n11, .Y .n12, .Y .n13, .Y .n14, .Y .n30, .Y .n301, .Y .n32, .Y .n33, .Y .n34, .Y .n35, .Y .n36, .Y .n37, .Y .n38, .Z .n37, .Z .n1, .Z .n2, .Z .n3, .Z .n4, .Z .n5, .Z .n6, .Z .n7, .Z .n8, .Z .n9, .Z .n36, .Z .n35, .Z .n34, .Z .n33, .Z .n32, .Z .n31, .Z .n30, .Z .n29, .Z .n28, .Z .n27 {
  display: none;
}

.X .n30, .X .n44, .Y .n45, .Y .n15  {
  margin-right: 75px;
}

.Y .n39, .Z .n38 {
